<template>
  <div>
    <Input placeholder="" @on-enter="inputComplete" v-model="inputTag"/>
    <ul class="tag-input">
      <li v-for="(field,index) in value" :key="index">
        <span>{{field}}</span>
        <Icon type="ios-close" size="14" @click="removeTag(index)"/>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "TagInput",
    data() {
      return {
        inputTag: '',
      }
    },
    props: {
      value: {
        type: Array,
        default() {
          return [];
        }
      }
    },
    methods: {
      removeTag(index) {
        this.value.splice(index, 1);
      },
      inputComplete() {
        if (this.inputTag) {
          let reg = /[a-zA-Z\u4e00-\u9fa5]\S*/;
          if (!reg.test(this.inputTag)) {
            this.$Message.warning("字段名称只能是中文或者字母开头");
            return;
          }
          let total = this.value.length;
          for (let i = 0; i < total; i++) {
            if (this.value[i] == this.inputTag) {
              this.$Message.warning("字段已经存在在字段列表中");
              return;
            }
          }
          this.value.push(this.inputTag);
          this.inputTag = '';
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .tag-input {
    display: inline-block;
    width: 100%;
    min-height: 32px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dcdee2;
    border-radius: 4px;
    color: #515a6e;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
    transition: border .2s ease-in-out, background .2s ease-in-out, box-shadow .2s ease-in-out;

    li {
      float: left;
      list-style: none;
      margin: 3px 3px;
      background: #E0EAF1;

      span {
        padding: 3px 10px;
        word-wrap: break-word;
        outline: none;
      }

      i {
        cursor: pointer;
        margin-right: 3px;
      }
    }
  }
</style>



