<template>
  <Form :model="processDesign" :label-width="50">
    <FormItem label="类型" class="input-item">
      <Input value="开始节点" placeholder="" disabled=""></Input>
    </FormItem>
    <FormItem label="显示名" class="input-item">
      <Input v-model="processDesign.label" placeholder=""></Input>
    </FormItem>
  </Form>
</template>

<script>
  export default {
    name: "StartInput",
    data() {
      return {
        form: {},
      }
    },
    props: {
      processDesign: {
        type: Object
      }
    }
  }
</script>

<style lang="scss" scoped>
  .input-item {
    margin-bottom: 10px !important;
  }
</style>



