<template>
  <Form :model="form" :label-width="50">
    <FormItem label="类型" class="input-item">
      <Input value="排他节点" placeholder="" disabled=""></Input>
    </FormItem>
    <FormItem label="显示名" class="input-item">
      <Input v-model="form.label" placeholder=""></Input>
    </FormItem>
    <FormItem label="字段" class="input-item">
      <tag-input v-model="selectFields"/>
    </FormItem>
  </Form>
</template>

<script>
  import TagInput from '../TagInput';

  export default {
    name: "ExclusiveInput",
    components: {
      TagInput
    },
    data() {
      return {
        form: {},
        fieldList: [],
        selectFields: [],
        selectIndex: -1,
      }
    },
    props: {
      value: {
        type: Object,
        default() {
          return {
            label: '',
            params: {
              fields: [],
            },
          }
        }
      },
      base: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    watch: {
      value: {
        handler(val1, val2) {
          this.parseData();
        },
        deep: true,
      },
      form: {
        handler(val1, val2) {
          this.$emit('input', this.form);
        },
        deep: true,
      },
      selectFields: {
        handler(val1, val2) {
          this.form.params = JSON.stringify(val1);
        },
        deep: true,
      }
    },
    methods: {
      parseData() {
        setTimeout(() => {
          this.form = this.value;
          this.selectFields = this.value.params ? JSON.parse(this.value.params) : [];
        }, 200);
      },
      initList() {
        this.$http.get(this.$api.finance.setting.getReimbusementTypeFieldList, {params: {typeCode: this.base.type}}).then(res => {
          let data = res.data;
          if (data.success === true) {
            this.fieldList = data.data;
          } else {
            this.$Message.warning(data.prompt);
          }
        }).catch(e => {
          this.$log(e, 4);
        });
      }
    },
    mounted() {
      this.parseData();
      // this.initList();
    }
  }
</script>

<style lang="scss" scoped>
  .input-item {
    margin-bottom: 10px !important;
  }

</style>



