<template>
  <g @mousedown.stop="nodeMouseDown($event)">
    <ellipse :cx="processDesign.x" :cy="processDesign.y" rx="40" ry="20" :class="{selected:processDesign.selected}"/>
    <text :x="processDesign.x-length*6" :y="processDesign.y+3" :class="{selected:processDesign.selected}">
      {{label}}
    </text>
  </g>
</template>

<script>
  export default {
    name: "EndNode",
    props: {
      processDesign: Object
    },
    computed: {
      label: {
        get() {
          if (this.processDesign.label.length > 4) {
            return this.processDesign.label.substring(0, 4) + '...'
          } else {
            return this.processDesign.label
          }
        }
      },
      length: {
        get() {
          return this.processDesign.label.length > 4 ? 5 : 4
        }
      }
    },
    methods: {
      nodeMouseDown(event) {
        this.$emit("mousedown", event);
      }
    }
  }
</script>

<style lang="scss" scoped>
ellipse {
  fill: red;
  //stroke: green;
  stroke-width: 2;
}

ellipse.selected {
  stroke: cornflowerblue;
}


g:hover {
  ellipse {
    stroke: cornflowerblue;
  }


  text {
    //fill: cornflowerblue;
  }
}

text {
  font-weight: 700;
  fill: white;
}

//text.selected {
//  fill: cornflowerblue;
//}
</style>



