<template>
  <Form :model="flowDesign" :label-width="80">
    <FormItem label="名称" class="input-item">
      <Input v-model="flowDesign.title" placeholder=""></Input>
    </FormItem>
    <FormItem label="失败模式" class="input-item" v-if="">
      <Select v-model="flowDesign.backMode">
        <Option :value="1">整体失败</Option>
        <Option :value="2">退回上一步</Option>
      </Select>
    </FormItem>
    <FormItem label="描述" class="input-item">
      <Input v-model="flowDesign.description" type="textarea" :rows="5" placeholder=""></Input>
    </FormItem>
    <FormItem label="结果抄送人" class="input-item">
      <Input placeholder="输入完毕按回车添加" @keyup.enter.native="handleAddUser"></Input>
      <div>
        <tag v-for="user in flowDesign.userList" :key="user" closable color="blue" type="border"
             @on-close="handleRemoveUser">
          {{ user }}
        </tag>
      </div>
    </FormItem>
  </Form>
</template>

<script>
  export default {
    name: "BaseInput",
    data() {
      return {
        form: {},
      }
    },
    props: {
      flowDesign: {
        type: Object
      }
    },
    methods: {
      handleAddUser(event) {
        const value = event.target.value
        if (value === '') {
          return
        }
        if (this.flowDesign.userList.indexOf(value) !== -1) {
          this.$Message.error('该用户已经存在了！')
          return
        }
        this.flowDesign.userList.push(value)
        event.target.value = ''
      },
      handleRemoveUser(value) {
        this.flowDesign.userList.splice(this.flowDesign.userList.indexOf(value, 1))
      },
    }
  }
</script>

<style lang="scss" scoped>
  .input-item {
    margin-bottom: 10px !important;
  }
</style>



