<template>
  <g @mousedown.stop="nodeMouseDown($event)">
    <rect width="80" height="40" :x="processDesign.x-40" :y="processDesign.y-20" rx="5" ry="5" :class="{selected:processDesign.selected}"/>
    <text :x="processDesign.x-processDesign.label.length*6" :y="processDesign.y+3" :class="{selected:processDesign.selected}">{{processDesign.label}}</text>
  </g>
</template>

<script>
  export default {
    name: "StartNode",
    props: {
      processDesign: Object,
      default() {
        return {
          type: 'start',
          x: 0,
          y: 0,
          label: '抄送节点',
          selected: false,
        }
      }
    },
    methods: {
      nodeMouseDown(event) {
        this.$emit("mousedown", event);
      }
    }
  }
</script>

<style lang="scss" scoped>
  rect {
    fill: rgb(255, 255, 255);
    stroke-width: 2;
    stroke: #31d0c6;
    border-radius: 5px;
  }

  rect.selected {
    stroke: #ff0000;
  }

  g:hover {
    rect {
      stroke: #ff0000;
    }

    text {
      fill: #ff0000;
    }
  }

  text {
    fill: #31d0c6;
  }

  text.selected {
    fill: #ff0000;
  }
</style>



