<template>
  <div class="flow-design-input">
    <template v-if="processDesign && processDesign.type">
      <component :is="processDesign.type" :processDesign="processDesign" :base="flowDesign"></component>
    </template>
    <template v-if="!processDesign || !processDesign.type">
      <base-input :flowDesign="flowDesign"/>
    </template>
  </div>
</template>

<script>
import ApprovalInput from './input/ApprovalInput';
import EndInput from './input/EndInput';
import ExclusiveInput from './input/ExclusiveInput';
import StartInput from './input/StartInput';
import TaskInput from './input/TaskInput';
import LinkLineInput from './input/LinkLineInput';
import BaseInput from './input/BaseInput';
import CopyInput from './input/CopyInput';
import ChildInput from './input/ChildInput';

export default {
  name: "FlowDesignInput",
  components: {
    start: StartInput,
    end: EndInput,
    approval: ApprovalInput,
    exclusive: ExclusiveInput,
    task: TaskInput,
    linkLine: LinkLineInput,
    copy: CopyInput,
    child: ChildInput,
    BaseInput,
  },
  props: {
    processDesign: {
      type: Object
    },
    flowDesign: {
      type: Object
    },
  },
  watch: {
    // processDesign: {
    //   handler(val1, val2) {
    //     if (this.processDesign) {
    //       let data = JSON.parse(JSON.stringify(this.processDesign));
    //       if (this.isFirst === true) {
    //         this.isFirst = false;
    //       } else {
    //         this.$emit('change-node', data);
    //       }
    //     }
    //   },
    //   deep: true
    // }
  }
}
</script>

<style lang="scss" scoped>
.flow-design-input {
  width: 100%;
  height: 100%;
  padding: 10px;
}
</style>



