<template>
  <g @mousedown.stop="nodeMouseDown($event)">
    <circle :cx="value.x" :cy="value.y" r="30" :class="{selected:value.selected}"/>
    <text :x="value.x-value.label.length*6" :y="value.y+3" :class="{selected:value.selected}">{{value.label}}</text>
  </g>
</template>

<script>
  export default {
    name: "TaskNode",
    props: {
      value: Object,
      default() {
        return {
          type: 'task',
          x: 0,
          y: 0,
          label: '任务节点',
          selected: false,
        }
      }
    },
    methods: {
      nodeMouseDown(event) {
        this.$emit("mousedown", event);
      }
    }
  }
</script>

<style lang="scss" scoped>
  circle {
    fill: rgb(255, 255, 255);
    stroke-width: 2;
    stroke: #31d0c6;
    border-radius: 5px;
  }

  circle.selected {
    stroke: #ff0000;
  }

  g:hover {
    circle {
      stroke: #ff0000;
    }

    text {
      fill: #ff0000;
    }
  }

  text {
    fill: #31d0c6;
  }

  text.selected {
    fill: #ff0000;
  }
</style>



