<template>
  <Form :model="form" :label-width="60">
    <FormItem label="类型" class="input-item">
      <Input value="抄送节点" placeholder="" disabled=""></Input>
    </FormItem>
    <FormItem label="显示名" class="input-item">
      <Input v-model="form.label" placeholder=""></Input>
    </FormItem>
    <FormItem label="抄送人" class="input-item">
<!--      <user-select v-model="params.objectList" :multiple="true"/>-->
    </FormItem>
  </Form>
</template>

<script>

  export default {
    name: "CopyInput",
    data() {
      return {
        isFirst: true,
        form: {},
        params: [],
      }
    },
    props: {
      value: {
        type: Object,
        default() {
          return {
            label: '',
            params: {},
          }
        }
      },
      base: {
        type: Object,
        default() {
          return {}
        }
      },
    },
    watch: {
      value: {
        handler(val1, val2) {
          this.isFirst = true;
          this.parseData();
        },
        deep: true,
      },
      form: {
        handler(val1, val2) {
          this.$emit('input', this.form);
        },
        deep: true,
      },
      params: {
        handler(val1, val2) {
          this.form.params = JSON.stringify(this.params);
        },
        deep: true,
      }
    },
    methods: {
      parseData() {
        setTimeout(() => {
          this.form = this.value;
          if (this.form.params) {
            let data = JSON.parse(this.value.params);
            this.params = data;
          } else {
            this.params = [];
          }
        }, 200);
      },
    },
    mounted() {
      this.isFirst = true;
      this.parseData();
    }
  }
</script>

<style lang="scss" scoped>
  .input-item {
    margin-bottom: 10px !important;
  }
</style>



