<template>

  <div class="flow-design">
    <div class="top-pane">
      <a href="javascript:void(0)" class="item" @click="saveDesign">保存</a>
      <a href="javascript:void(0)" class="item" @click="removeSelected">删除</a>
      <a href="javascript:void(0)" class="item" @click="linkLine">连线</a>
      <a href="javascript:void(0)" class="item" @click="initData()">重新加载</a>
      <a href="javascript:void(0)" class="item" @click="doBack()">返回</a>
    </div>
    <div class="bottom-pane">
      <div class="left-menu">
        <div v-for="(bean,index) in flowTypes" class="menu-item" :type="bean.type" draggable="true"
             @dragstart="drag($event,bean.type)">
          {{ bean.label }}
        </div>
      </div>
      <div class="center-content" @drop="drop($event)" @dragover="allowDrop($event)">
        <!--面包屑显示上级-->
        <flow-design-nodes ref="flowDesign" :flowDesign="flowDesign" @selected="nodeSelected"
                           :width="flowDesign.width" :height="flowDesign.height" :base="flowDesign"/>
      </div>
      <div class="right-property">
        <flow-design-input :processDesign="selectedNode" :flowDesign="flowDesign" @change-node="nodeDataChange">
        </flow-design-input>
      </div>
    </div>


    <Spin size="large" fix v-if="spinShow">
      <Icon type="ios-loading" size="18" class="demo-spin-icon-load"></Icon>
      <div>加载中...</div>
    </Spin>
  </div>
</template>

<script>
import FlowDesignNodes from './flow/FlowDesignNodes';
import FlowDesignInput from './flow/FlowDesignInput';
import {deploy, design, reset} from "../api/processApi.js";

export default {
  name: "FlowDesign",
  data() {
    return {
      flowTypes: [
        {
          type: 'start',
          label: '开始节点',
        },
        {
          type: 'approval',
          label: '审批节点',
        },
        // {
        //   type: 'exclusive',
        //   label: '排他节点',
        // },
        // {
        //   type: 'task',
        //   label: '任务节点',
        // },
        // {
        //   type: 'copy',
        //   label: '抄送节点',
        // },
        // {
        //   type: 'child',
        //   label: '子流程',
        // },
        {
          type: 'end',
          label: '结束节点',
        },
      ],
      flowDesign: {},
      spinShow: false,
      selectedNode: null,
    }
  },
  components: {
    FlowDesignNodes,
    FlowDesignInput,
  },
  methods: {
    nodeDataChange(data) {
      this.$refs.flowDesign.changeSelected(data);
    },
    nodeSelected(node) {
      this.selectedNode = node;
    },
    getCurrentData() {
      let form = JSON.parse(JSON.stringify(this.flowDesign));
      if (form.processDesigns) {
        form.processDesigns.forEach(item => {
          if (item.type == 'linkLine') {
            item.target = item.target.sid;
            item.source = item.source.sid;
          }
        })
      }
      let rect = this.$refs.flowDesign.$el.getBoundingClientRect();
      form.width = rect.width;
      form.height = rect.height;
      return form;
    },
    saveDesign: function () {
      this.spinShow = true;
      let form = this.getCurrentData();

      //判断是否存在开始节点和结束节点以及至少有一个审批节点
      let nodeTypes = form.processDesigns.map(item => item.type)
      if (nodeTypes.indexOf('start') === -1 || nodeTypes.indexOf('end') === -1 || nodeTypes.indexOf('approval') === -1) {
        this.$Message.error('流程设计不完整，请检查！')
        this.spinShow = false
        return
      }
      //判断连线是否完整
      if (this.getArrayEleCount(nodeTypes, 'linkLine', false) !== (this.getArrayEleCount(nodeTypes, 'linkLine', true) - 1)) {
        this.$Message.error('连线有误，请检查！')
        this.spinShow = false
        return
      }
      //将审批节点转换为Map集合
      let map = new Map()
      form.processDesigns.filter(item => item.type !== 'linkLine').forEach(item => map.set(item.sid, item))
      //寻找开始节点的线
      const startLine = form.processDesigns.filter(item => item.type === 'linkLine' && map.get(item.source) && map.get(item.source).type === 'start')[0];
      //获取审批第一个节点
      const firstNode = map.get(startLine.target)
      //重构节点
      this.refactorNode(firstNode, 1)
      //构建设计
      this.buildDesign(form, map, firstNode.sid, 2)
      //转换抄送人
      form.receiveUser = form.userList.join(',')
      delete form.userList

      //判断是否是重置
      if (form.sid) {
        //重置
        reset(form).then(res => {
          if (res.data.code === 200) {
            this.$Message.success("重置成功");
            this.doBack();
          } else {
            this.$Message.error(res.data.message ? res.data.message : '系统异常')
          }
          setTimeout(() => {
            this.spinShow = false;
          }, 200);
        }).catch(error => {
          this.$Message.error(error)
        })
      } else {
        //部署
        deploy(form).then(res => {
          if (res.data.code === 200) {
            this.$Message.success("保存成功");
            this.doBack();
          } else {
            this.$Message.error(res.data.message ? res.data.message : '系统异常')
          }
          setTimeout(() => {
            this.spinShow = false;
          }, 200);
        }).catch(error => {
          this.$Message.error(error ? error : '系统异常')

          setTimeout(() => {
            this.spinShow = false;
          }, 200);
        })
      }
    },
    buildDesign(form, map, sid, ordered) {
      const lineNode = form.processDesigns.filter(item => item.type === 'linkLine' && map.get(item.source) && map.get(item.source).sid === sid && map.get(item.target).type !== 'end' && map.get(item.target))[0];
      if (lineNode) {
        //设置源节点字段
        const params = JSON.parse(map.get(sid).params)
        params.field = lineNode.field
        params.condition = lineNode.condition
        params.value = lineNode.value
        //重置参数
        map.get(sid).params = JSON.stringify(params)
        //删除线的参数
        delete lineNode.field
        delete lineNode.condition
        delete lineNode.value
        //获取目标节点
        const targetNode = map.get(lineNode.target)
        //重构节点
        this.refactorNode(targetNode, ordered)
        //继续递归
        this.buildDesign(form, map, targetNode.sid, ++ordered)
      }
    },
    //重构节点
    refactorNode(node, ordered) {
      //获取参数
      const params = JSON.parse(node.params)
      //设置顺序
      params.ordered = ordered
      //设置标题
      params.title = node.label
      //设置用户
      params.user = node.userList.join(',')
      //设置角色
      params.role = node.roleList.join(',')
      //删除用户集合和角色集合
      delete node.userList
      delete node.roleList
      //重置参数
      node.params = JSON.stringify(params)
    },
    removeSelected() {
      this.$Modal.confirm({
        title: "提示",
        content: "<p>您确认删除吗</p>",
        onOk: () => {
          this.$refs.flowDesign.deleteSelected();
          this.selectedNode = null
        },
        onCancel: () => {
        }
      });
    },
    linkLine() {
      this.$refs.flowDesign.addLine();
    },
    allowDrop(ev) {
      ev.preventDefault();
    },
    drag(ev, type) {
      ev.dataTransfer.setData("flow_type", type);
    },
    drop(ev) {
      ev.preventDefault();
      let type = ev.dataTransfer.getData("flow_type");
      let name = '';
      for (let i = 0; i < this.flowTypes.length; i++) {
        if (this.flowTypes[i].type === type) {
          name = this.flowTypes[i].label;
          break;
        }
      }
      this.$refs.flowDesign.addNodeNode(type, name);
    },
    initData() {
      const sid = this.$route.query.sid
      if (sid) {
        design(sid).then(res => {
          this.flowDesign = this.parseData(res.data.body)
        }).catch(error => {
          this.$Message.error(error)
        })
      } else {
        this.spinShow = true;
        this.flowDesign = {processDesigns: [], title: '', description: '', backMode: 2, sid: null, userList: []};
        setTimeout(() => {
          this.spinShow = false;
        }, 200);
      }
    },
    doBack() {
      this.$router.push({
        path: '/flowList'
      })
    },
    parseData(body) {
      //转换为map集合
      const map = new Map()
      //转换抄送人
      body.userList = body.receiveUser ? body.receiveUser.split(',') : []
      body.processDesigns.forEach(item => {
        map.set(item.sid, item)
        item.selected = false
        //判断是否是审批节点
        if ('approval' === item.type) {
          const params = JSON.parse(item.params)
          item.userList = params.user ? params.user.split(',') : []
          item.roleList = params.role ? params.role.split(',') : []
        }
        //判断是否是连线
        if ('linkLine' === item.type) {
          //获取源
          const source = map.get(item.source)
          item.source = source
          item.target = map.get(item.target)

          //设置线段参数
          const params = source.params ? JSON.parse(source.params) : {}
          item.field = params.field
          item.condition = params.condition
          item.value = params.value
        } else {
          item.x = item.x - 0
          item.y = item.y - 0
        }
      })

      return body
    },
    getArrayEleCount(array, ele, isNegate) {
      return array.filter(item => isNegate ? item !== ele : item === ele).length
    }
  },
  mounted() {
    this.initData();
  }
}
</script>

<style lang="scss" scoped>
.flow-design {
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
  border: 1px solid #cccccc;


  .history {
    background: #ffffff;
    display: inline-block;
    position: absolute;
    z-index: 1;
    padding: 2px 5px;
    user-select: none;

    .path-item {
      font-size: 14px;
      display: inline-block;
      padding: 0px 5px;
      color: #222222;
    }

    a.path-item {
      cursor: pointer;
    }

    a:hover {
      color: blue;
    }

    .path-item.active {
      font-weight: bold;
    }
  }


  .top-pane {
    flex: 0 0 40px;
    box-sizing: border-box;
    border-bottom: 1px solid #cccccc;
    padding-left: 10px;
    text-align: left;

    .item {
      display: inline-block;
      height: 40px;
      padding: 10px 10px;
      color: #000;
    }

    .item:hover {
      color: #2b85e4;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  .bottom-pane {
    flex: 1 1 auto;
    display: flex;
    flex-flow: row;

    .left-menu {
      flex: 0 0 100px;
      border-right: 1px solid #cccccc;
      user-select: none;

      .menu-item {
        width: 80px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        border: 1px solid #c8c9cc;
        margin: 10px 10px;
        cursor: pointer;
        border-radius: 5px;
      }

      .menu-item:hover {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }

    .center-content {
      flex: 1 1 auto;
      background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPjxkZWZzPjxwYXR0ZXJuIGlkPSJhIiBwYXR0ZXJuVW5pdHM9InVzZXJTcGFjZU9uVXNlIiB3aWR0aD0iMTAiIGhlaWdodD0iMTAiPjxwYXRoIGZpbGw9IiNBQUEiIGQ9Ik0wIDBoMXYxSDB6Ii8+PC9wYXR0ZXJuPjwvZGVmcz48cmVjdCB3aWR0aD0iMTAwJSIgaGVpZ2h0PSIxMDAlIiBmaWxsPSJ1cmwoI2EpIi8+PC9zdmc+);
      overflow: hidden;
      position: relative;
    }

    .right-property {
      flex: 0 0 400px;
      border-left: 1px solid #cccccc;
    }
  }
}
</style>



