<template>
  <div>
    <Form :model="form" :label-width="60">
      <FormItem label="类型" class="input-item">
        <Input value="子流程节点" placeholder="" disabled=""></Input>
      </FormItem>
      <FormItem label="显示名" class="input-item">
        <Input v-model="form.label" placeholder=""></Input>
      </FormItem>
      <FormItem label="选择流程" class="input-item">
        <Select v-model="params.flowId">
          <Option v-for="bean in list" :value="bean.id" :key="bean.id" v-if="bean.id!=base.id">{{bean.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="" class="input-item">
        <Button @click="addFlowDesign">添加</Button>
      </FormItem>
    </Form>

    <Modal v-model="showInput" @on-ok="saveBean" title="设计器管理" width="700" ref="accountBooksWin">
      <Form :model="addForm" :label-width="100" ref="form">
        <FormItem label="设计器名称" prop="name">
          <Input v-model="addForm.name" placeholder="设计器名称"></Input>
        </FormItem>
        <FormItem label="失败退回" prop="failType">
          <Select v-model="addForm.failType">
            <Option value="first_step">退回第一步</Option>
            <Option value="last_step">退回上一步</Option>
          </Select>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>

  export default {
    name: "ChildInput",
    data() {
      return {
        showInput: false,
        isFirst: true,
        form: {},
        addForm: {
          name: '',
          failType: 'first_step',
        },
        list: [],
        params: {
          flowId: '',
        }
      }
    },
    props: {
      value: {
        type: Object,
        default() {
          return {
            label: '',
            params: {},
          }
        }
      },
      base: {
        type: Object,
        default() {
          return {}
        }
      },
    },
    watch: {
      value: {
        handler(val1, val2) {
          this.isFirst = true;
          this.parseData();
        },
        deep: true,
      },
      form: {
        handler(val1, val2) {
          this.$emit('input', this.form);
        },
        deep: true,
      },
      params: {
        handler(val1, val2) {
          this.form.params = JSON.stringify(this.params);
        },
        deep: true,
      }
    },
    methods: {
      saveBean() {
        if (!this.addForm.name) {
          this.$Message.warning("请输入正确的名称");
          this.$refs.accountBooksWin.visible = true;
          this.showInput = true;
          return;
        }
        this.$http.post(this.$api.finance.setting.postFlowDesignPageSaveOrUpdateMsg, this.addForm).then(res => {
          let data = res.data;
          if (data.success === true) {
            this.$Message.info("操作成功");
            this.params.flowId = data.data;
            this.initList();
          } else {
            this.$refs.accountBooksWin.visible = true;
            this.showInput = true;
            this.$Message.warning(data.prompt);
          }
        }).catch(e => {
          this.$refs.accountBooksWin.visible = true;
          this.showInput = true;
          this.$log(e, 4);
        });
      },
      addFlowDesign() {
        this.addForm.name = '';
        this.showInput = true;
      },
      parseData() {
        setTimeout(() => {
          this.form = this.value;
          if (this.form.params) {
            let data = JSON.parse(this.value.params);
            this.params = data;
          } else {
            this.params = {
              flowId: '',
            };
          }
        }, 200);
      },
    },
    mounted() {
      this.isFirst = true;
      this.parseData();
    }
  }
</script>

<style lang="scss" scoped>
  .input-item {
    margin-bottom: 10px !important;
  }
</style>



