<template>
  <g @mousedown.stop="nodeMouseDown($event)">
    <path :d="path" :class="{selected:processDesign.selected}" class="line-path"></path>
    <text :x="labelX" :y="labelY" :class="{selected:processDesign.selected}">
      {{ label }}
    </text>
    <path :d="endPath" :transform="endRotate" :class="{selected:processDesign.selected}" class="arrow-path"/>
  </g>
</template>

<script>
export default {
  name: "LinkLine",
  data() {
    return {
      labelX: 0,
      labelY: 0,
      path: '',
      endPath: '',
      endRotate: '',
      params: {
        type: 0,
      },
    }
  },
  props: {
    processDesign: Object
  },
  watch: {
    processDesign: {
      handler(val1, val2) {
        this.parseData();
      },
      deep: true,
    }
  },
  mounted() {
    this.parseData();
  },
  computed: {
    label: {
      get() {
        if (this.processDesign.label.length > 8) {
          return this.processDesign.label.substring(0, 8) + '...'
        } else {
          return this.processDesign.label
        }
      }
    }
  },
  methods: {
    parseData() {
      setTimeout(() => {
        this.params = this.processDesign.params ? JSON.parse(this.processDesign.params) : {
          type: 0,
          resultType: 1,
          mergeType: 'and',
          fieldList: []
        };
        //计算开始和结束点
        let sourcePoint = this.getSourcePoint();
        let targetPoint = this.getTargetPoint();
        //计算两点的角度
        let angle = Math.atan2((targetPoint[1] - sourcePoint[1]), (targetPoint[0] - sourcePoint[0])) //弧度
        let theta = angle * (180 / Math.PI);

        //标签位置
        this.labelX = (sourcePoint[0] + targetPoint[0]) / 2;
        this.labelY = (sourcePoint[1] + targetPoint[1]) / 2;
        //连线
        if (theta < 45 && theta >= -45) {
          this.path = "M" + sourcePoint[0] + " " + sourcePoint[1] + " L" + this.labelX + " " + sourcePoint[1] + " L" + this.labelX + " " + targetPoint[1] + " L" + targetPoint[0] + " " + targetPoint[1];
          //箭头角度
          this.endRotate = 'rotate(' + 0 + ',' + targetPoint[0] + ' ' + targetPoint[1] + ')';
        } else if (-45 > theta && theta >= -135) {
          this.path = "M" + sourcePoint[0] + " " + sourcePoint[1] + " L" + sourcePoint[0] + " " + this.labelY + " L" + targetPoint[0] + " " + this.labelY + " L" + targetPoint[0] + " " + targetPoint[1];
          //箭头角度
          this.endRotate = 'rotate(' + -90 + ',' + targetPoint[0] + ' ' + targetPoint[1] + ')';
        } else if (135 <= theta || theta < -135) {
          this.path = "M" + sourcePoint[0] + " " + sourcePoint[1] + " L" + this.labelX + " " + sourcePoint[1] + " L" + this.labelX + " " + targetPoint[1] + " L" + targetPoint[0] + " " + targetPoint[1];
          //箭头角度
          this.endRotate = 'rotate(' + -180 + ',' + targetPoint[0] + ' ' + targetPoint[1] + ')';
        } else if (theta < 135 && theta >= 45) {
          this.path = "M" + sourcePoint[0] + " " + sourcePoint[1] + " L" + sourcePoint[0] + " " + this.labelY + " L" + targetPoint[0] + " " + this.labelY + " L" + targetPoint[0] + " " + targetPoint[1];
          //箭头角度
          this.endRotate = 'rotate(' + 90 + ',' + targetPoint[0] + ' ' + targetPoint[1] + ')';
        }
        //箭头
        this.endPath = "M" + (targetPoint[0] - 10) + " " + (targetPoint[1] - 6) + " L" + (targetPoint[0] - 10) + " " + (targetPoint[1] + 6) + " L" + targetPoint[0] + " " + targetPoint[1];
      }, 10);
    },
    getTargetPoint() {
      let target = this.processDesign.target;
      if (target.type == 'temp') {
        return [target.x, target.y];
      }
      let source = this.processDesign.source;
      return this.getPoint(target.x, target.y, 80, 40, source.x, source.y);
    },
    getSourcePoint() {
      let target = this.processDesign.target;
      let source = this.processDesign.source;
      return this.getPoint(source.x, source.y, 80, 40, target.x, target.y);
    },
    getPoint(x, y, w, h, x1, y1) {
      let arr = [x, y - h / 2];
      let distance = Math.sqrt(Math.pow(x - x1, 2) + Math.pow((y - h / 2) - y1, 2));
      let distance1 = Math.sqrt(Math.pow((x + w / 2) - x1, 2) + Math.pow(y - y1, 2));
      if (distance1 < distance) {
        distance = distance1;
        arr = [(x + w / 2), y];
      }
      distance1 = Math.sqrt(Math.pow(x - x1, 2) + Math.pow((y + h / 2) - y1, 2));
      if (distance1 < distance) {
        distance = distance1;
        arr = [x, y + h / 2];
      }
      distance1 = Math.sqrt(Math.pow((x - w / 2) - x1, 2) + Math.pow(y - y1, 2));
      if (distance1 < distance) {
        distance = distance1;
        arr = [(x - w / 2), y];
      }
      return arr;
    },
    nodeMouseDown(event) {
      this.$emit("mousedown", event);
    }
  }
}
</script>

<style lang="scss" scoped>
.line-path {
  stroke-width: 2;
  fill: none;
  stroke: slategrey;
}

.line-path.selected {
  stroke: cornflowerblue;
  fill: none;
}

.arrow-path {
  stroke-width: 2;
  fill: slategrey;
  stroke: slategrey;
}

.arrow-path.selected {
  stroke: cornflowerblue;
  fill: cornflowerblue;
}

text {
  font-weight: 700;
  fill: black;
}

text.selected {
  fill: cornflowerblue;
}


g:hover {
  .arrow-path {
    stroke: cornflowerblue;
    fill: cornflowerblue;
  }

  .line-path {
    stroke: cornflowerblue;
  }

  text {
    fill: cornflowerblue;
  }
}
</style>



