<template>
  <div class="flow-design-nodes" id="flow_design_svg" @mousemove="mouseMove($event)" @mouseup="mouseUp($event)">
    <svg width="2000" height="2000" version="1.1"
         xmlns="http://www.w3.org/2000/svg" @DOMMouseScroll="scrollBarWheel($event)"
         @mousewheel.passive="scrollBarWheel($event)"
         @mousedown="svgMouseDown($event)" xmlns:xlink="http://www.w3.org/1999/xlink">
      <defs>
        <marker id="arrow" markerWidth="10" markerHeight="10" refx="0" refy="3" orient="auto" markerUnits="strokeWidth">
          <path d="M0,0 L0,6 L9,3 z" fill="#f00"/>
        </marker>
      </defs>
      <g :transform="'translate('+left+' '+top+') scale('+scale+' '+scale+')'">
        <template v-for="(bean,index) in flowDesign.processDesigns">
          <component :is="bean.type" :processDesign="bean" @mousedown="nodeMouseDown($event,index)" :base="flowDesign">
          </component>
        </template>
      </g>
    </svg>
  </div>
</template>

<script>

  import StartNode from './node/StartNode';
  import EndNode from './node/EndNode';
  import ApprovalNode from './node/ApprovalNode';
  import ExclusiveNode from './node/ExclusiveNode';
  import TaskNode from './node/TaskNode';
  import LinkLine from './node/LinkLine';
  import CopyNode from './node/CopyNode';
  import ChildNode from './node/ChildNode';

  export default {
    name: "FlowDesignComponent",
    components: {
      start: StartNode,
      end: EndNode,
      approval: ApprovalNode,
      exclusive: ExclusiveNode,
      task: TaskNode,
      linkLine: LinkLine,
      copy: CopyNode,
      child: ChildNode,
    },
    props: {
      flowDesign: {
        type: Object
      },
      width: {
        type: Number,
        default: 0,
      },
      height: {
        type: Number,
        default: 0,
      }
    },
    data() {
      return {
        scale: 1,
        Origin: '0 0',
        left: 0,
        top: 0,
        selected: {},
        mode: 'point',  //point line svg
        oldX: 0,
        oldY: 0,
      }
    },
    watch: {
      width: {
        handler(val1, val2) {
          this.parseScale();
        }
      },
      height: {
        handler(val1, val2) {
          this.parseScale();
        }
      }
    },
    mounted() {
      this.parseScale();
      window.onresize = () => { // 定义窗口大小变更通知事件
        this.parseScale();
      };
    },
    methods: {
      parseScale() {
        let rect = this.$el.getBoundingClientRect();
        let scaleX = 0;
        let scaleY = 0;
        if (this.width > 0 || this.height > 0) {
          if (this.width > 0) {
            scaleX = rect.width / this.width;
          }
          if (this.height > 0) {
            scaleY = rect.height / this.height;
          }
        }
        if (scaleX > 0 && scaleY > 0) {
          this.scale = scaleY < scaleX ? scaleY : scaleX;
        } else if (scaleY > 0 && scaleX == 0) {
          this.scale = scaleY;
        } else if (scaleY == 0 && scaleX > 0) {
          this.scale = scaleX;
        } else {
          this.scale = 1;
        }
      },
      changeSelected(data) {
        this.selected.label = data.label;
        this.selected.params = data.params;
      },
      deleteSelected() {
        if (this.selected) {
          for (let i = this.flowDesign.processDesigns.length - 1; i >= 0; i--) {
            let node = this.flowDesign.processDesigns[i];
            if (node == this.selected) {
              this.flowDesign.processDesigns.splice(i, 1);
              continue;
            }
            if (node.type == 'linkLine' && (node.target == this.selected || node.source == this.selected)) {
              this.flowDesign.processDesigns.splice(i, 1);
            }
          }
        }
      },
      addNodeNode(type, name) {
        if (type == 'start' || type == 'end') {
          for (let i = 0; i < this.flowDesign.processDesigns.length; i++) {
            if (this.flowDesign.processDesigns[i].type == type) {
              this.$Message.warning("该类型节点只能有一个");
              return;
            }
          }
        }
        let rect = this.$el.getBoundingClientRect();
        let x = (event.clientX - rect.left - this.left) / this.scale;
        let y = (event.clientY - rect.top - this.top) / this.scale;
        this.unSelect();

        //定义节点
        const node = {
          sid: this.flowDesign.processDesigns.length,
          x: x,
          y: y,
          type: type,
          label: name,
          selected: true,
          params: ''
        }
        if(type === 'approval'){
          node.userList = []
          node.roleList = []
          node.params = JSON.stringify({
            title: '',
            description: '',
            department: '',
            user: '',
            role: '',
            mode: 2
          })
        }
        this.flowDesign.processDesigns.push(node);
      },
      addLine() {
        console.info("开始连线");
        this.unSelect();
        this.selected = null;
        this.mode = 'line';
      },
      unSelect() {
        this.flowDesign.processDesigns.forEach((item) => {
          item.selected = false;
        })
      },
      scrollBarWheel(event) {
        let num = event.deltaY || event.detail
        if (num > 0) {
          this.scale = this.scale - this.scale * 0.01;
        } else {
          this.scale = this.scale + this.scale * 0.01;
        }
      },
      svgMouseDown(event) {
        this.unSelect();
        if (this.selected) {
          if (this.mode == 'line') {
            this.flowDesign.processDesigns.splice(this.lineList.length - 1, 1);
          }
        }
        this.mode = 'svg';
        this.oldX = event.clientX;
        this.oldY = event.clientY;
        this.$emit("selected", null);
      },
      nodeMouseDown(event, index) {
        this.unSelect();
        this.oldX = event.clientX;
        this.oldY = event.clientY;
        //连线
        if (this.mode == 'line') {
          let rect = this.$el.getBoundingClientRect();
          if (this.selected) {
            console.info("连线结束")
            this.selected.target = this.flowDesign.processDesigns[index];
            this.mode = 'point';
          } else {
            let x = (event.clientX - rect.left - this.left) / this.scale;
            let y = (event.clientY - rect.top - this.top) / this.scale;
            this.selected = {
              sid: this.flowDesign.processDesigns.length,
              type: 'linkLine',
              label: '',
              selected: true,
              source: this.flowDesign.processDesigns[index],
              target: {id: '', x: x, y: y, type: 'temp'},
              params: '',
            };
            this.flowDesign.processDesigns.push(this.selected);
          }
          //选中操作
        } else if (this.mode == 'point') {
          this.selected = this.flowDesign.processDesigns[index];
          this.selected.selected = true;
          this.mode = 'drag';
        }
        this.$emit("selected", this.selected);
      },
      lineMouseDown(event, index) {
        this.unSelect();
        this.selected = this.flowDesign.processDesigns[index];
        this.selected.selected = true;
        this.$emit("selected", this.selected);
      },
      mouseMove(event) {
        let rect = this.$el.getBoundingClientRect();
        if (this.mode == 'drag') {
          if (this.selected) {
            let addX = (event.clientX - this.oldX) / this.scale;
            let addY = (event.clientY - this.oldY) / this.scale;
            this.selected.x += addX;
            this.selected.y += addY;
            this.selected.x = this.selected.x < 0 ? 0 : (this.selected.x > 2000 ? 2000 : this.selected.x);
            this.selected.y = this.selected.y < 0 ? 0 : (this.selected.y > 2000 ? 2000 : this.selected.y);
            this.oldX = event.clientX;
            this.oldY = event.clientY;
          }
        } else if (this.mode == 'svg') {
          let addX = event.clientX - this.oldX;
          let addY = event.clientY - this.oldY;
          this.oldX = event.clientX;
          this.oldY = event.clientY;
          this.left += addX;
          this.top += addY;
        } else if (this.mode == 'line') {
          if (this.selected) {
            let x = (event.clientX - rect.left - this.left) / this.scale;
            let y = (event.clientY - rect.top - this.top) / this.scale;
            this.selected.target.x = x - 3;
            this.selected.target.y = y - 3;
          }
        }
      },
      mouseUp(event) {
        if (this.mode === 'point') {
        } else if (this.mode == 'svg') {
          this.mode = 'point';
          this.selected = null;
        } else if (this.mode === 'drag') {
          this.mode = 'point';
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .flow-design-nodes {
    width: 100%;
    height: 100%;

    svg {
      user-select: none;
    }
  }
</style>



