<template>
  <g @mousedown.stop="nodeMouseDown($event)" @dblclick.stop="toChild">
    <rect width="80" height="40" :x="value.x-40" :y="value.y-20" rx="5" ry="5" :class="{selected:value.selected}"/>
    <text :x="value.x-value.label.length*6" :y="value.y+3" :class="{selected:value.selected}">{{value.label}}</text>
  </g>
</template>

<script>
  export default {
    name: "StartNode",
    props: {
      value: Object,
      default() {
        return {
          type: 'child',
          x: 0,
          y: 0,
          label: '子流程',
          selected: false,
          params: "",
        }
      }
    },
    methods: {
      nodeMouseDown(event) {
        this.$emit("mousedown", event);
      },
      toChild() {
        if (this.value.params) {
          let data = JSON.parse(this.value.params);
          if (data && data.flowId) {
            this.$emit("to-child", data.flowId);
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  rect {
    fill: rgb(255, 255, 255);
    stroke-width: 2;
    stroke: #31d0c6;
    border-radius: 5px;
  }

  rect.selected {
    stroke: #ff0000;
  }

  g:hover {
    rect {
      stroke: #ff0000;
    }

    text {
      fill: #ff0000;
    }
  }

  text {
    fill: #31d0c6;
  }

  text.selected {
    fill: #ff0000;
  }
</style>



