<template>
  <Form :model="processDesign" :label-width="60">
    <FormItem label="类型" class="input-item">
      <Input value="审批节点" placeholder="" disabled=""></Input>
    </FormItem>
    <FormItem label="显示名" class="input-item">
      <Input v-model="processDesign.label" placeholder=""></Input>
    </FormItem>
    <FormItem label="描述" class="input-item">
      <Input v-model="params.description" placeholder="" type="textarea" :rows="5"></Input>
    </FormItem>
    <FormItem label="审批类型" class="input-item">
      <Select v-model="params.mode">
        <Option :value="2">或签</Option>
        <Option :value="1">会签</Option>
      </Select>
    </FormItem>
    <FormItem label="部门" class="input-item">
      <Input v-model="params.department" placeholder=""></Input>
    </FormItem>
    <FormItem label="审核用户" class="input-item">
      <Input placeholder="输入完毕按回车添加" @keyup.enter.native="handleAddUser"></Input>
      <div>
        <tag v-for="user in processDesign.userList" :key="user" closable color="blue" type="border"
             @on-close="handleRemoveUser">
          {{ user }}
        </tag>
      </div>
    </FormItem>
    <FormItem label="审核角色" class="input-item">
      <Input placeholder="输入完毕按回车添加" @keyup.enter.native="handleAddRole"></Input>
      <div>
        <tag v-for="role in processDesign.roleList" :key="role" closable color="blue" type="border"
             @on-close="handleRemoveRole">
          {{ role }}
        </tag>
      </div>
    </FormItem>
  </Form>
</template>

<script>

export default {
  name: "ApprovalInput",
  data() {
    return {
      isFirst: true,
      params: {
        userList: [],
        roleList: []
      }
    }
  },
  props: {
    processDesign: {
      type: Object
    },
    base: {
      type: Object,
      default() {
        return {}
      }
    },
  },
  watch: {
    processDesign: {
      handler(val1, val2) {
        this.initParams()
      },
      deep: true,
    },
    params: {
      handler(val1, val2) {
        this.processDesign.params = JSON.stringify(this.params);
      },
      deep: true,
    }
  },
  methods: {
    handleAddUser(event) {
      const value = event.target.value
      if (value === '') {
        return
      }
      if (this.processDesign.userList.indexOf(value) !== -1) {
        this.$Message.error('该用户已经存在了！')
        return
      }
      this.processDesign.userList.push(value)
      event.target.value = ''
    },
    handleAddRole(event) {
      const value = event.target.value
      if (value === '') {
        return
      }
      if (this.processDesign.roleList.indexOf(value) !== -1) {
        this.$Message.error('该角色已经存在了！')
        return
      }
      this.processDesign.roleList.push(value)
      event.target.value = ''
    },
    handleRemoveUser(value) {
      this.processDesign.userList.splice(this.processDesign.userList.indexOf(value, 1))
    },
    handleRemoveRole(value) {
      this.processDesign.roleList.splice(this.processDesign.roleList.indexOf(value, 1))
    },
    initParams(){
      this.isFirst = true;
      this.params = this.processDesign.params ? JSON.parse(this.processDesign.params) : {
        userList: [],
        roleList: []
      }
    }
  },
  created() {
    this.initParams()
  }
}
</script>

<style lang="scss" scoped>
.input-item {
  margin-bottom: 10px !important;
}
</style>



