<template>
  <g @mousedown.stop="nodeMouseDown($event)">
    <rect width="80" height="40" :x="processDesign.x-40" :y="processDesign.y-20" rx="5" ry="5"
          :class="{selected:processDesign.selected}"/>
    <text :x="processDesign.x-length * 6" :y="processDesign.y+3"
          :class="{selected:processDesign.selected}">{{ label }}
    </text>
  </g>
</template>

<script>
export default {
  name: "ApprovalNode",
  props: {
    processDesign: Object
  },
  computed: {
    label: {
      get() {
        if (this.processDesign.label.length > 4) {
          return this.processDesign.label.substring(0, 4) + '...'
        } else {
          return this.processDesign.label
        }
      }
    },
    length: {
      get() {
        return this.processDesign.label.length > 4 ? 5 : 4
      }
    }
  },
  methods: {
    nodeMouseDown(event) {
      this.$emit("mousedown", event);
    }
  }
}
</script>

<style lang="scss" scoped>
rect {
  fill: #fa541c;
  stroke-width: 2;
  //stroke: #31d0c6;
  //stroke: lightcyan;
  border-radius: 5px;
}

rect.selected {
  stroke: cornflowerblue;
}

g:hover {
  rect {
    stroke: cornflowerblue;
  }

  //text {
  //  fill: #ff0000;
  //}
}

text {
  font-weight: 700;
  fill: white;
}

//text.selected {
//  fill: #ff0000;
//}
</style>



