<template>
  <g @mousedown.stop="nodeMouseDown($event)">
    <polygon :points="point" :class="{selected:value.selected}"/>
    <text :x="value.x-value.label.length*6" :y="value.y+3" :class="{selected:value.selected}">
      {{value.label}}
    </text>
  </g>
</template>

<script>
  export default {
    name: "ExclusiveNode",
    data() {
      return {
        width: 100,
        height: 60,
      };
    },
    props: {
      value: Object,
      default() {
        return {
          type: 'exclusive',
          x: 0,
          y: 0,
          label: '审批节点',
          selected: false,
        }
      }
    },
    computed: {
      point() {
        let left = (this.value.x - 40) + ',' + this.value.y;
        let top = this.value.x + ',' + (this.value.y - 20);
        let right = (this.value.x + 40) + ',' + this.value.y;
        let bottom = this.value.x + ',' + (this.value.y + 20);
        return left + ' ' + top + ' ' + right + ' ' + bottom;
      }
    },
    methods: {
      nodeMouseDown(event) {
        this.$emit("mousedown", event);
      }
    }
  }
</script>

<style lang="scss" scoped>
  polygon {
    fill: rgb(255, 255, 255);
    stroke-width: 2;
    stroke: #31d0c6;
    border-radius: 5px;
  }

  polygon.selected {
    stroke: #ff0000;
  }

  g:hover {
    polygon {
      stroke: #ff0000;
    }

    text {
      fill: #ff0000;
    }
  }

  text {
    fill: #31d0c6;
  }

  text.selected {
    fill: #ff0000;
  }
</style>



